import { AfterViewInit, Component, ElementRef, ViewChild } from '@angular/core';
import { PartnersService } from '../../../services/partners.service';
import { AbstractControl, FormBuilder, FormGroup, ValidationErrors, Validators, FormArray } from '@angular/forms';
import { TranslationService } from '../../../services/translation.service';
import { PartnersConfirmationModalComponent } from '../partners-confirmation-modal/partners-confirmation-modal.component';

@Component({
  selector: 'app-add-partner',
  standalone: false,
  templateUrl: './add-partner.html',
  styleUrl: './add-partner.css'
})
export class AddPartner implements AfterViewInit {
  @ViewChild('confirmationModal') confirmationModal!: PartnersConfirmationModalComponent;
  
  // Custom modal properties
  isOpen = false;
  
  addForm!: FormGroup
  
  constructor(
    private partnersService: PartnersService,
    private fb: FormBuilder,
    private translationService: TranslationService
  ) {}

  ngAfterViewInit(): void {
    // No Bootstrap modal needed
  }
  
  open() { 
    this.isOpen = true;
    // Reset form when opening
    this.addForm.reset();
    this.contacts.clear();
    this.addContact(); // Add initial contact
  }
  
  close() { 
    this.isOpen = false;
    // Reset form when closing
    this.addForm.reset();
    this.contacts.clear();
    this.countrySearchTerm = '';
    this.filteredCountries = [...this.countries];
  }

  // Handle backdrop click
  onBackdropClick(event: Event) {
    if (event.target === event.currentTarget) {
      this.close();
    }
  }

  readonly categories = [
    { key: 0, value: "partners.categories.0" },
    { key: 1, value: "partners.categories.1" },
    { key: 2, value: "partners.categories.2" },
    { key: 3, value: "partners.categories.3" },
    { key: 4, value: "partners.categories.4" },
    { key: 5, value: "partners.categories.5" }
  ]

  readonly countries = [
    "Afghanistan", "Albania", "Algeria", "Andorra", "Angola", "Antigua and Barbuda", "Argentina", "Armenia", "Australia", "Austria",
    "Azerbaijan", "Bahamas", "Bahrain", "Bangladesh", "Barbados", "Belarus", "Belgium", "Belize", "Benin", "Bhutan",
    "Bolivia", "Bosnia and Herzegovina", "Botswana", "Brazil", "Brunei", "Bulgaria", "Burkina Faso", "Burundi", "Cabo Verde", "Cambodia",
    "Cameroon", "Canada", "Central African Republic", "Chad", "Chile", "China", "Colombia", "Comoros", "Congo", "Costa Rica",
    "Croatia", "Cuba", "Cyprus", "Czech Republic", "Democratic Republic of the Congo", "Denmark", "Djibouti", "Dominica", "Dominican Republic", "Ecuador",
    "Egypt", "El Salvador", "Equatorial Guinea", "Eritrea", "Estonia", "Eswatini", "Ethiopia", "Fiji", "Finland", "France",
    "Gabon", "Gambia", "Georgia", "Germany", "Ghana", "Greece", "Grenada", "Guatemala", "Guinea", "Guinea-Bissau",
    "Guyana", "Haiti", "Honduras", "Hungary", "Iceland", "India", "Indonesia", "Iran", "Iraq", "Ireland",
    "Israel", "Italy", "Ivory Coast", "Jamaica", "Japan", "Jordan", "Kazakhstan", "Kenya", "Kiribati", "Kuwait",
    "Kyrgyzstan", "Laos", "Latvia", "Lebanon", "Lesotho", "Liberia", "Libya", "Liechtenstein", "Luxembourg", "Madagascar",
    "Malawi", "Malaysia", "Maldives", "Mali", "Malta", "Marshall Islands", "Mauritania", "Mauritius", "Mexico", "Micronesia",
    "Moldova", "Monaco", "Mongolia", "Montenegro", "Morocco", "Mozambique", "Myanmar", "Namibia", "Nauru", "Nepal",
    "Netherlands", "New Zealand", "Nicaragua", "Niger", "Nigeria", "North Korea", "North Macedonia", "Norway", "Oman", "Pakistan",
    "Palau", "Palestine", "Panama", "Papua New Guinea", "Paraguay", "Peru", "Philippines", "Poland", "Portugal", "Qatar",
    "Romania", "Russia", "Rwanda", "Saint Kitts and Nevis", "Saint Lucia", "Saint Vincent and the Grenadines", "Samoa", "San Marino", "Sao Tome and Principe", "Saudi Arabia",
    "Senegal", "Serbia", "Seychelles", "Sierra Leone", "Singapore", "Slovakia", "Slovenia", "Solomon Islands", "Somalia", "South Africa",
    "South Korea", "South Sudan", "Spain", "Sri Lanka", "Sudan", "Suriname", "Sweden", "Switzerland", "Syria", "Taiwan",
    "Tajikistan", "Tanzania", "Thailand", "Timor-Leste", "Togo", "Tonga", "Trinidad and Tobago", "Tunisia", "Turkey", "Turkmenistan",
    "Tuvalu", "Uganda", "Ukraine", "United Arab Emirates", "United Kingdom", "United States", "Uruguay", "Uzbekistan", "Vanuatu", "Vatican City",
    "Venezuela", "Vietnam", "Yemen", "Zambia", "Zimbabwe"
  ];

  filteredCountries: string[] = [];
  countrySearchTerm: string = '';

  private futureDateValidator(control: AbstractControl): ValidationErrors | null {
    if (!control.value) return null;

    const inputDate = new Date(control.value);
    const today = new Date();
    today.setHours(0, 0, 0, 0); // ignore time

    return inputDate < today ? { pastDate: true } : null;
  }

  ngOnInit() {
    this.initializeForm();
    this.filteredCountries = [...this.countries];
  }

  filterCountries(searchTerm: string) {
    if (!searchTerm.trim()) {
      this.filteredCountries = [...this.countries];
    } else {
      this.filteredCountries = this.countries.filter(country =>
        country.toLowerCase().includes(searchTerm.toLowerCase())
      );
    }
  }

  onCountrySearch(event: any) {
    const searchTerm = event.target.value.toLowerCase();
    this.countrySearchTerm = searchTerm;
    this.filteredCountries = this.countries.filter(country => 
      country.toLowerCase().includes(searchTerm)
    );
  }

  initializeForm() {
    this.addForm = this.fb.group({
      name: ['', [Validators.required, Validators.minLength(3), Validators.maxLength(15)]],
      category: ['', Validators.required],
      sector: ['', [Validators.required, Validators.minLength(2)]],
      country: ['', [Validators.required, Validators.minLength(2)]],
      city: ['', [Validators.required, Validators.minLength(2)]],
      note: ['', [Validators.required, Validators.minLength(5)]],
      description: ['', [Validators.required, Validators.minLength(10)]],
      website: ['', [Validators.required, Validators.minLength(5)]],
      address: ['', [Validators.required, Validators.minLength(5)]],
      contractStartDate: ['', [Validators.required, this.futureDateValidator]],
      status: ['', Validators.required],
      contacts: this.fb.array([])
    });
    
    // Add initial contact
    this.addContact();
  }

  // Contacts management methods
  get contacts() {
    return this.addForm.get('contacts') as FormArray;
  }

  addContact() {
    const contactGroup = this.fb.group({
      email: ['', [Validators.required, Validators.email]],
      phone: ['', [Validators.required, Validators.minLength(10)]],
      mobile: ['', [Validators.required, Validators.minLength(10)]]
    });
    this.contacts.push(contactGroup);
  }

  removeContact(index: number) {
    if (this.contacts.length > 1) {
      this.contacts.removeAt(index);
    }
  }

  getContactErrorMessage(control: AbstractControl | null, field: string): string {
    if (!control?.errors) return '';
    
    if (control.errors['required']) {
      return this.translate('common.field_required');
    }
    if (control.errors['email']) {
      return this.translate('common.invalid_email');
    }
    if (control.errors['minlength']) {
      const requiredLength = control.errors['minlength']['requiredLength'];
      return `Minimum length is ${requiredLength} characters`;
    }
    return 'Invalid field';
  }

  isFieldValid(field: string) {
    const control = this.addForm.get(field);
    return control?.touched && control?.invalid
  }

  hasServerError(field: string) {
    const control = this.addForm.get(field);
    return control?.errors?.['serverError'];
  }

  getServerErrorMessage(field: string) {
    const control = this.addForm.get(field);
    return control?.errors?.['serverError'] || '';
  }

  getErrorMessage(field: string) {
    const control = this.addForm.get(field);
    if (!control?.errors) return ''
    
    if (control.errors['required']) {
      return this.translate('common.field_required')
    }
    if (control.errors['minlength']) {
      const requiredLength = control.errors['minlength']['requiredLength']
      if (requiredLength === 3) {
        return this.translate('partners.min_length_3')
      } else if (requiredLength === 5) {
        return this.translate('partners.min_length_5')
      } else if (requiredLength === 10) {
        return this.translate('partners.min_length_10')
      }
      return `Minimum length is ${requiredLength} characters`
    }
    if (control.errors['maxlength']) {
      const requiredLength = control.errors['maxlength']['requiredLength']
      if (requiredLength === 15) {
        return this.translate('partners.max_length_15')
      }
      return `Maximum length is ${requiredLength} characters`
    }
    if (control.errors['pastDate']) {
      return this.translate('partners.future_date_required')
    }
    return 'Invalid field'
  }

  onSubmit() {
    if (this.addForm.invalid) {
      this.addForm.markAllAsTouched()
      return
    }

    const formValue = this.addForm.value;
    
    // Ensure all required fields are included and properly formatted
    const partnerData = {
      name: formValue.name,
      category: parseInt(formValue.category),
      sector: formValue.sector,
      country: formValue.country,
      city: formValue.city,
      note: formValue.note,
      description: formValue.description,
      website: formValue.website,
      address: formValue.address,
      contractStartDate: new Date(formValue.contractStartDate).toISOString(),
      status: parseInt(formValue.status)
    };


    this.partnersService.addPartner(partnerData).subscribe({
      next: (res) => {
        this.showSuccessMessage(this.translate('partners.created_success'));
        this.close()
        this.addForm.reset()
        // Optionally refresh the partners list
        // this.partnersList.loadPartners();
      },
      error: (error) => {
        this.handleApiErrors(error);
      }
    })
  }

  private handleApiErrors(error: any) {
    if (error.error?.errors) {
      // Handle validation errors by setting them on form controls
      const errors = error.error.errors;
      
      Object.keys(errors).forEach(fieldName => {
        const control = this.addForm.get(this.getFormFieldName(fieldName));
        if (control) {
          control.setErrors({ serverError: errors[fieldName][0] });
          control.markAsTouched();
        }
      });
    } else {
      // Show generic error in alert
      let errorMessage = 'Unknown error occurred';
      if (error.error?.message) {
        errorMessage = error.error.message;
      } else if (error.message) {
        errorMessage = error.message;
      }
      alert(this.translationService.translate('common.error') + ': ' + errorMessage);
    }
  }

  private getFormFieldName(apiFieldName: string): string {
    // Map API field names (capitalized) to form field names (lowercase)
    const fieldMapping: { [key: string]: string } = {
      'Name': 'name',
      'Category': 'category',
      'Sector': 'sector',
      'Country': 'country',
      'City': 'city',
      'Note': 'note',
      'Description': 'description',
      'Website': 'website',
      'Address': 'address',
      'ContractStartDate': 'contractStartDate',
      'Status': 'status'
    };
    return fieldMapping[apiFieldName] || apiFieldName.toLowerCase();
  }

  translate(key: string): string {
    return this.translationService.translate(key);
  }

  isRTL(): boolean {
    return this.translationService.isRTL();
  }

  showSuccessMessage(message: string) {
    const successData = {
      title: this.translate('common.success'),
      message: message,
      confirmText: this.translate('common.ok'),
      cancelText: '',
      type: 'info' as const
    };
    this.confirmationModal.show(successData);
  }
}
