import { Component, OnInit, ViewChild } from '@angular/core';
import { PartnersService, PartnerSearchParams } from '../../../services/partners.service';
import { Contact, Item, PartnerModel } from '../../../models/partner.model';
import { TranslationService } from '../../../services/translation.service';
import { PartnersConfirmationModalComponent } from '../partners-confirmation-modal/partners-confirmation-modal.component';
import { EditContactsModalComponent } from '../edit-contacts-modal/edit-contacts-modal.component';
import { AddPartner } from '../add-partner/add-partner';
import { EditPartner } from '../edit-partner/edit-partner';

@Component({
  selector: 'app-partners-list',
  standalone: false,
  templateUrl: './partners-list.html',
  styleUrl: './partners-list.css'
})
export class PartnersList implements OnInit {
  @ViewChild('addPartnerModal') addPartnerModal!: AddPartner;
  @ViewChild('editPartnerModal') editPartnerModal!: EditPartner;
  @ViewChild('editContactsModal') editContactsModal!: EditContactsModalComponent;
  @ViewChild('confirmationModal') confirmationModal!: PartnersConfirmationModalComponent;
  
  allPartners: Item[] = [];
  filteredPartners: Item[] = [];
  searchTerm: string = '';
  isLoading: boolean = false;
  expandedContacts: { [key: number]: boolean } = {};
  
  // Pagination
  currentPage = 1;
  pageSize = 6;
  totalCount = 0;
  selectedCategory: number | null = null;
  
  constructor(
    private partnersService: PartnersService,
    private translationService: TranslationService
  ) {}

  get categories(): Record<number, string> {
    return {
      0: this.translate('partners.categories.0'),
      1: this.translate('partners.categories.1'),
      2: this.translate('partners.categories.2'),
      3: this.translate('partners.categories.3'),
      4: this.translate('partners.categories.4'),
      5: this.translate('partners.categories.5')
    };
  }

  ngOnInit() {
    this.loadPartners();
  }

  loadPartners() {
    this.isLoading = true;
    
    const params: PartnerSearchParams = {
      Search: this.searchTerm || undefined,
      PageSize: this.pageSize,
      PageNumber: this.currentPage,
      Category: this.selectedCategory || undefined
    };
    
    this.partnersService.getAllPartners(params).subscribe({
      next: (res) => {
        this.allPartners = res.data.items;
        this.totalCount = res.data.totalCount;
        this.currentPage = res.data.page;
        this.pageSize = res.data.pageSize;
        
        // Initialize contacts from partner data if available
        this.allPartners.forEach(partner => {
          if (!partner.contacts) {
            partner.contacts = [];
          }
        });
        
        // Load contacts for each partner and wait for all to complete
        const contactPromises = this.allPartners.map((partner) => {
          return new Promise<void>((resolve) => {
            // Skip if partner already has contacts from the API response
            if (partner.contacts && partner.contacts.length > 0) {
              resolve();
              return;
            }
            
            this.partnersService.getContacts(partner.id).subscribe({
              next: (contacts) => {
                partner.contacts = contacts || [];
                resolve();
              },
              error: (error) => {
                // Keep existing contacts if any, otherwise set empty array
                if (!partner.contacts) {
                  partner.contacts = [];
                }
                resolve();
              }
            });
          });
        });
        
        // Wait for all contacts to load before updating the UI
        Promise.all(contactPromises).then(() => {
          this.filteredPartners = [...this.allPartners];
          this.isLoading = false;
        });
      },
      error: (error) => {
        this.isLoading = false;
      }
    });
  }

  onSearchChange() {
    this.currentPage = 1;
    this.loadPartners();
  }

  onSearchClick() {
    this.currentPage = 1;
    this.loadPartners();
  }

  onCategoryChange(category: number | null) {
    this.selectedCategory = category;
    this.currentPage = 1;
    this.loadPartners();
  }

  onPageChange(page: number) {
    this.currentPage = page;
    this.loadPartners();
  }

  onPageSizeChange() {
    this.currentPage = 1;
    this.loadPartners();
  }

  clearFilters() {
    this.searchTerm = '';
    this.selectedCategory = null;
    this.currentPage = 1;
    this.loadPartners();
  }

  getCategoryLabel(category: number): string {
    return this.categories[category] || 'Unknown';
  }

  getCategoryLabelAr(category: number): string {
    const arCategories: Record<number, string> = {
      0: "حكومي",
      1: "أكاديمي",
      2: "خاص",
      3: "منظمة غير حكومية",
      4: "دولي",
      5: "أخرى"
    };
    return arCategories[category] || 'غير معروف';
  }

  getContactsDisplay(contacts: Contact[]): string {
    if (!contacts || contacts.length === 0) {
      return '';
    }
    
    const displayContacts = contacts.slice(0, 2);
    const contactStrings = displayContacts.map(contact => 
      `${contact.email} - ${contact.phone} - ${contact.mobile}`
    );
    
    if (contacts.length > 2) {
      return contactStrings.join(' | ') + ` +${contacts.length - 2} more`;
    }
    
    return contactStrings.join(' | ');
  }

  openAddModal() {
    this.addPartnerModal.open();
  }

  deletePartner(id: number, name: string) {
    const confirmData = {
      title: this.translationService.translate('partners.delete_partner'),
      message: this.translationService.translate('partners.confirm_delete_message').replace('%name%', name),
      confirmText: this.translationService.translate('common.delete'),
      cancelText: this.translationService.translate('common.cancel'),
      type: 'delete' as const
    };

    this.confirmationModal.show(confirmData);
    this.confirmationModal.confirm.subscribe(() => {
      // User confirmed deletion
      this.partnersService.deletePartner(id).subscribe({
        next: (res) => {
          this.allPartners = this.allPartners.filter(p => p.id !== id);
          this.onSearchChange(); // Refresh filtered list
          this.showSuccessMessage(this.translationService.translate('partners.deleted_success'));
        },
        error: (error) => {
          const errorMessage = error.error?.message || error.message || 'Unknown error occurred';
          this.showErrorMessage(this.translationService.translate('common.error') + ': ' + errorMessage);
        }
      });
    });
  }

  showSuccessMessage(message: string) {
    const successData = {
      title: this.translationService.translate('common.success'),
      message: message,
      confirmText: this.translationService.translate('common.ok'),
      cancelText: '',
      type: 'info' as const
    };
    this.confirmationModal.show(successData);
  }

  showErrorMessage(message: string) {
    const errorData = {
      title: this.translationService.translate('common.error'),
      message: message,
      confirmText: this.translationService.translate('common.ok'),
      cancelText: '',
      type: 'warning' as const
    };
    this.confirmationModal.show(errorData);
  }

  toggleContacts(partnerId: number) {
    this.expandedContacts[partnerId] = !this.expandedContacts[partnerId];
  }

  editPartnerContacts(partner: Item, event?: Event) {
    // Prevent event bubbling to avoid toggling contacts when clicking edit button
    if (event) {
      event.stopPropagation();
    }
    // Open contacts editing modal
    this.editContactsModal.show(partner);
  }

  refreshPartnerContacts(partnerId: number) {
    // Refresh contacts for a specific partner
    const partner = this.allPartners.find(p => p.id === partnerId);
    if (partner) {
      this.partnersService.getContacts(partnerId).subscribe({
        next: (contacts) => {
          partner.contacts = contacts || [];
          // Update filtered partners if needed
          const filteredIndex = this.filteredPartners.findIndex(p => p.id === partnerId);
          if (filteredIndex !== -1) {
            this.filteredPartners[filteredIndex].contacts = contacts || [];
          }
          // Force change detection
          this.filteredPartners = [...this.filteredPartners];
        },
        error: (error) => {
          // Handle error silently
        }
      });
    }
  }

  debugContacts() {
    // Debug method removed
  }

  editPartner(partner: Item) {
    // Open edit modal with partner data
    this.editPartnerModal.open(partner);
  }

  translate(key: string): string {
    return this.translationService.translate(key);
  }

  isRTL(): boolean {
    return this.translationService.isRTL();
  }

  get totalPages(): number {
    return Math.ceil(this.totalCount / this.pageSize);
  }

  get pages(): number[] {
    const pages: number[] = [];
    const maxVisiblePages = 5;
    
    if (this.totalPages === 0) {
      pages.push(1);
      return pages;
    }
    
    let startPage = Math.max(1, this.currentPage - Math.floor(maxVisiblePages / 2));
    let endPage = Math.min(this.totalPages, startPage + maxVisiblePages - 1);
    
    if (endPage - startPage + 1 < maxVisiblePages) {
      startPage = Math.max(1, endPage - maxVisiblePages + 1);
    }
    
    for (let i = startPage; i <= endPage; i++) {
      pages.push(i);
    }
    
    return pages;
  }

  get Math() {
    return Math;
  }
}
