import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { environment } from '../../environments/environment';
import { Observable } from 'rxjs';
import { Contact, CreatePartnerDto, Item, PartnerModel } from '../models/partner.model';

export interface PartnerSearchParams {
  Search?: string;
  PageSize?: number;
  PageNumber?: number;
  Category?: number;
}

@Injectable({
  providedIn: 'root'
})
export class PartnersService {
  
  private apiUrl=`${environment.apiBaseUrl}/Partners`;
  constructor(private http:HttpClient) { }

  getAllPartners(params: PartnerSearchParams = {}):Observable<PartnerModel>{
    let url = this.apiUrl;
    const queryParams = new URLSearchParams();
    
    if (params.Search) queryParams.append('Search', params.Search);
    if (params.PageSize) queryParams.append('PageSize', params.PageSize.toString());
    if (params.PageNumber) queryParams.append('PageNumber', params.PageNumber.toString());
    if (params.Category !== undefined) queryParams.append('Category', params.Category.toString());
    
    if (queryParams.toString()) {
      url += '?' + queryParams.toString();
    }
    
    return this.http.get<PartnerModel>(url);
  }
  addPartner(partner:CreatePartnerDto):Observable<any>{
    return this.http.post<any>(this.apiUrl,partner);
  }
  updatePartner(id:number,partner:PartnerModel):Observable<any>{
    return this.http.put<any>(`${this.apiUrl}/${id}`,partner);
  }
  getPartner(id:number):Observable<any>{
    return this.http.get<any>(`${this.apiUrl}/${id}`);
  }
  getContacts(partnerId:number):Observable<Contact[]>{
    return this.http.get<Contact[]>(`${this.apiUrl}/${partnerId}/contacts`);
  }
  
  addContact(partnerId: number, contact: Contact): Observable<Contact> {
    return this.http.post<Contact>(`${this.apiUrl}/${partnerId}/contacts`, contact);
  }
  
  updateContact(partnerId: number, contactId: number, contact: Contact): Observable<Contact> {
    return this.http.put<Contact>(`${this.apiUrl}/${partnerId}/contacts/${contactId}`, contact);
  }
  
  deleteContact(partnerId: number, contactId: number): Observable<any> {
    const url = `${this.apiUrl}/${partnerId}/contacts/${contactId}`;
    console.log('API Call - Delete Contact:', {
      url,
      partnerId,
      contactId,
      fullUrl: `${environment.apiBaseUrl}/Partners/${partnerId}/contacts/${contactId}`
    });
    return this.http.delete(url, {
      observe: 'response'
    });
  }
  
  deletePartner(id:number){
    return this.http.delete(`${this.apiUrl}/${id}`);
  }

}
